import { Card, Grid, Stack, Typography } from '@mui/material';
import { useState } from 'react';
import Keyboard from 'react-simple-keyboard';
import 'react-simple-keyboard/build/css/index.css';
import TopBar from './common/TopBar';
import '../styles/keyboard.css';
import { useRef } from 'react';


export default function GuessCapitalName({ country, previousLevel, nextLevel }) {

    const [input, setInput] = useState("");
    const [isInputSubmitted, setInputSubmitted] = useState(false);
    const [capitalCharacters, setCapitalCharacters] = useState(capitalToCharacters())

    const keyboard = useRef();
    const capital = country.capital.name;

    function capitalToCharacters() {
        return [...country.capital.name].map(char => {
            return {
                char: char,
                isGuessed: false
            }
        })
    }

    const onChange = (input) => {
        setInput(input);
    }

    function onPressGuess() {
        setInputSubmitted(true);
        processSumbittedInput();
    }

    const onKeyPress = (button) => {
        if (button == "{GUESS}") {
            onPressGuess();
        } else if (isInputSubmitted) {
            setInput("");
            keyboard.current.clearInput();
            setInputSubmitted(false);
        }
    }

    function processSumbittedInput() {
        // Register new guessed characters
        [...input].forEach((char, index) => {
            if (capitalCharacters[index].char === char) {
                capitalCharacters[index].isGuessed = true;
            }
        })
        console.log(capitalCharacters);

        // Green buttons
        [...capitalCharacters].filter(character => character.isGuessed)
            .forEach(character => keyboard.current.addButtonTheme(character.char, "successButton"));

        // // New orange buttons
        // [...input].filter((char, index) => isAlmost(char, index, input))
        //     .forEach(key => keyboard.current.addButtonTheme(key, "almostButton"));


        // Grey buttons
        [...input].filter(char => !capital.includes(char))
            .forEach(key => keyboard.current.addButtonTheme(key, "excludedButton"));
    }

    function countOccurences(char, source) {
        let split = source.split(char);
        let occurences = split.length - 1;
        return occurences;
    }

    return (
        <Stack
            justifyContent="space-between"
            alignItems="center"
            spacing={1}
            sx={{
                height: '100vh',

                /* mobile viewport bug fix */
                minHeight: '-webkit-fill-available ',
                maxHeight: '-webkit-fill-available',
            }}
            dispay='flex'
        >
            <TopBar victory={false} title={"Level 3"} previousLevel={previousLevel} nextLevel={nextLevel} />

            <Grid
                container
                spacing={2}
                justifyContent="center"
                paddingRight={2}
            >
                {[...capitalCharacters].map((character, index) => {
                    return (
                        <Grid item xs={1} key={index} minWidth={40}>
                            {input?.length > index ?
                                <CharCard
                                    char={input.charAt(index)}
                                    guessed={capitalCharacters[index].isGuessed}
                                />
                                :
                                <EmptyCard
                                    guessed={capitalCharacters[index].isGuessed}
                                />
                            }
                        </Grid>
                    )
                })}
            </Grid>

            <Keyboard
                keyboardRef={r => (keyboard.current = r)}
                onChange={onChange}
                onKeyPress={onKeyPress}
                display={{
                    '{backspace}': '←',
                    '{GUESS}': 'GUESS'
                }}
                layout={{
                    'default': [
                        'Q W E R T Y U I O P',
                        'A S D F G H J K L',
                        'Z X C V B N M {backspace}',
                        '{GUESS}'
                    ]
                }}
                physicalKeyboardHighlight={true}
                physicalKeyboardHighlightPress={true}
                maxLength={capitalCharacters.length}
            />

        </Stack >
    )

    function EmptyCard({ guessed }) {
        return <Card
            elevation={4}
            square
            sx={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                backgroundColor: guessed ? 'success.light' : null
            }}
        >
            <Typography variant="h6">
                &nbsp;
            </Typography>
        </Card>;
    }

    function GreenCard({ char }) {
        return <CharCard char={char} color={'success.light'} />
    }

    function CharCard({ char, guessed }) {
        return <Card
            elevation={4}
            square
            sx={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                backgroundColor: guessed ? 'success.light' : null
            }}
        >
            <Typography variant="h6">
                {char}
            </Typography>
        </Card>;
    }
}